<template>
    <div>
        <slider2></slider2>
        <slist :list="list" :dataName="dataName"></slist>
    </div>
</template>

<script>
    import slider2 from './slider2.vue';
    import slist from './slist.vue';
      import {mapGetters} from "vuex";
    export default{
        computed:mapGetters([
                "news"
                ]),
        data(){
            return{
                dataName:'speak',
                list:[]
            }
        },
        components:{
            slider2,slist
        },
        mounted(){
          	this.$http({
			url:'http://localhost:8000/news',
			method:'get',
			// withCredentials:true,//跨域凭证
                }).then((res)=>{
                    this.$store.dispatch("Cnews",res.data)
                    // console.log(res.data)
                }).catch((res)=>{
                    // console.log(res)
                })
        }
    }
</script>


