<template>
  <div>
    <div class="member">
			<div class="member-pic">
				<img src="/static/images/default_photo.png" />
			</div>
			<div class="member-infor">{{userData[0]}}</div>
            <span class="reg"  @click="tuichu">注销</span>
	</div>
    <ul class="member-nav">
			<li>
                <a href="">
                    <i class="am-icon-map-marker"></i>
                    <span>收货地址</span>
                </a>
            </li>
			<li>
                <a href="">
                    <i class="am-icon-newspaper-o"></i>
                    <span>我的订单</span>
