			<li>
                <a href="">
                    <i class="am-icon-credit-card"></i>
                    <span>会员卡</span>
                </a>
            </li>
			<li>
                <a href="#">
                    <i class="am-icon-cc-mastercard"></i>
                    <span>优惠券</span>
                </a>
            </li>
			<li>
                <a href="">
                    <i class="am-icon-dollar"></i>
                    <span>积分</span>
                </a>
            </li>
		</ul>
		<ul class="member-nav mt">
			<li>
                <a href=""><i class="am-icon-phone"></i>联系我们</a>
            </li>
		</ul>
      </div>
</template>

<script>
