<template>
    <div>
        <ul class="nav nav2">
			<li>
				<a href="#">
					<img src="/static/images/ms.jpg" />
					<p>美食快拍</p>
				</a>
			</li>
			<li>
				<a href="#">
					<img src="/static/images/cy.jpg" />
					<p>厨艺交流</p>
				</a>
			</li>
			<li>
				<a href="#">
					<img src="/static/images/mmp.jpg" />
					<p>妈妈派</p>
				</a>
			</li>
			<li>
				<a href="#">
					<img src="/static/images/ys.jpg" />
					<p>饮食健康</p>
				</a>
			</li>
		</ul>
		  <div data-am-widget="intro" class="am-intro am-cf am-intro-default">
		    <div class="am-intro-hd">
		        <h2 class="am-intro-title">功效</h2>
		    </div>
			<router-link v-for="item of news" :key="item.id" tag="div" class="am-g am-intro-bd" to="content">
		    	<a href="#">
			        <div class="am-intro-left am-u-sm-3"><img src="/static/images/p.png"  /></div>
			        <div class="am-intro-right am-u-sm-9">
			        	<h2>{{item.title}}</h2>
			        	<p>{{item.con}}</p>
			        	<div class="text">
			        		<span class="fl">{{item.time}}</span>
			        		<span class="fr"><i class="am-icon-thumbs-o-up"></i>&nbsp;点赞</span>
			        	</div>
			        </div>
		       </a>
		    </router-link>

		  </div>
    </div>
</template>

<script>
import {mapGetters} from "vuex";
	export default{
			computed:mapGetters([
                "news"
            ]),
			props:{
				list:{
					type:Array,
					default:[{id:1,title:'暂无标题',price:'暂无描述'}]
				},
				dataName:{
					type:String,
					required:true
				}
			}
			
		}
</script>


